<template>
  <scroll class="content"
          ref="scroll"
          :probe-type="3"
          :pull-up-load="true">
    <play-list-item v-for="(song, index) in list"
                    :song="song"
                    :key="index"
                    @imgLoad="imgLoad"
                    @click.native="clickListItem(song)"/>
  </scroll>
</template>

<script>
  import Scroll from 'components/common/scroll/Scroll'
  import PlayListItem from './PlayListitem'

  import { debounce } from 'common/utils'

  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: 'PlayList',
    components: {
      Scroll,
      PlayListItem,
    },
    props: {
      list: {
        type: Array,
        default: []
      },
    },
    data() {
      return {
        saveY: 0,
        refresh: null,
      }
    },
    mounted() {
      // refresh
      this.refresh = debounce(this.$refs.scroll.refresh, 200)
    },
    methods: {
      ...mapActions(['replacePlayList', 'changeMusic']),
      imgLoad() {
        this.refresh()
      },
      clickListItem(song) {
        // 
        let currentListId = this.playList.listId
        if (currentListId !== this.listId) {
          let list = {
            list: this.list,
            listId: this.listId
          }
          this.replacePlayList(list)
        }

        // 
        this.changeMusic(song)
      }
    },
    computed: {
      ...mapGetters(['playList']),
      // id
      listId() {
        let listId = ''
        for (const item of this.list) {
          listId += item.id
        }
        return listId
      },
    }
  }
</script>

<style scoped>
  .content {
    height: calc(100vh - 110px)
  }
</style>